.background {
  background-color: #0b2f6c;
  height: 100vh;
}

h1 {
  text-align: center;
  color: white;
  position: relative;
  top: 25%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  font-size: x-large;
  font-weight: 700;
  text-transform: uppercase;
}

button {
  position: relative;
  top: 35%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-sizing: border-box;
  appearance: none;
  background-color: transparent;
  border: 2px solid white;
  border-radius: 0.6em;
  color: white;
  cursor: pointer;
  text-decoration: none;
  text-transform: uppercase;
  font-weight: 700;
  width: 185px;
  height: 50px;
}
button:hover{
  font-weight: 600;
  box-shadow: 3px 3px 3px  white;
}

.caution{
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
  text-align: center;
  width: 100%;
  font-size: medium;
  font-weight: 700;
  text-transform: uppercase;
}


@media only screen and (min-width: 768px){
  .caution{
    width: 55%;
  }
}



